const TemplateMetroStation1 = ({
  name,
  href,
  desc,
  parent,
  images = [],
  ...content
}) => {
  // useEffect(() => {
  //   console.log(['useEffect.mount'], 'template3')
  //   return () => {
  //     console.log(['useEffect.unmount'], 'template3')
  //   };
  // })

  console.log(["stacja"], content);
  const view = useSelector((state) => state.view);
  console.log(["view"], view);
  return (
    <div>
      <div><a href={`#${parent}`}>&#171; Powrót</a>Stacja: {name}</div>
      <div style={{
        background: 'white',
        position: 'absolute'
      }}>
        Linia:
        {view[parent].list
          .map((path) => ({ path, ...view[path] }))
          .map(({ path, name }) =>
            path === content.path ? (
              <span style={{ margin: 10 }}>{name}</span>
            ) : (
              <a href={`#${path}`} style={{ display: 'block', margin: 10 }}>
                {name}
              </a>
            )
          )}
      </div>
      {images.map((src, key) =>
        href ? (
          <a key={key} href={href} target="_blank" rel="noopener noreferrer">
            <img src={src} alt={name} />
          </a>
        ) : (
          <img key={key} src={src} alt={name} />
        )
      )}
      {desc && <Format>{desc}</Format>}
    </div>
  );
};

const TemplateMetroLine1 = () => {
  // useEffect(() => {
  //   console.log(['useEffect.mount'], 'template3')
  //   return () => {
  //     console.log(['useEffect.unmount'], 'template3')
  //   };
  // })
  return <div>TemplateMetroLine1</div>;
};

export const Format = ({ children }) => (
  <div dangerouslySetInnerHTML={{ __html: marked(children) }} />
);

export const METRO_TEMPLATES = {
  [TEMPLATE_METRO_LINE_1]: TemplateMetroLine1,
  [TEMPLATE_METRO_STATION_1]: TemplateMetroStation1,
  [TEMPLATE_METRO_LINE_2]: ({ path, ...props }) => {
    const [active, setActive] = useState(null);
    const view = useSelector((state) => state.view);

    return (
      console.log(["metro2"], props) || (
        <div>
          Metro II
          <div>
            Linia:
            {view[path].list
              .map((path) => ({ path, ...view[path] }))
              .map(({ path, name }) => (
                <a key={path} href={`#${path}`} style={{ margin: 10, color: path === active ? 'red' : 'black' }} onMouseEnter={e => setActive(path)}>
                  {name}
                </a>
              ))}
          </div>
          {active && (
            <div style={{
              position: 'absolute',
              right: 10,
              border: '2px solid red',
              padding: 10
            }}><a href={`#${active}`}>{view[active].name}</a></div>
          )}
          <img
            src={require("../assets/I_II_linia07-2019.png")}
            style={{ width: 600 }}
            alt=""
          />
          m_trocka
        </div>
      )
    );
  },
  [TEMPLATE_METRO]: ({ ...props }) =>
    console.log(["metro"], props) || (
      <div>
        Metro
        <a href="#/metro/ii/Rondo%20Daszyńskiego/">rondo daszyńskiego</a>
        <img
          src={require("../assets/I_II_linia nazwy stacji 2019.png")}
          style={{ width: 600 }}
          alt=""
        />
      </div>
    ),
};

