const mapMetro1Station = (name) =>
  (({ name, ...rest }) => ({
    name,
    path: `/metro/i/${name}/`,
    parent: `/metro/i/`,
    template: TEMPLATE_METRO_STATION_1,
    ...rest,
  }))(
    typeof name === "string"
      ? {
          name,
        }
      : name
  );
const mapMetro2Station = (name) =>
  (({ name, ...rest }) => ({
    name,
    path: `/metro/ii/${name}/`,
    parent: `/metro/ii/`,
    template: TEMPLATE_METRO_STATION_1,
    ...rest,
  }))(
    typeof name === "string"
      ? {
          name,
        }
      : name
  );

