const next = () => (dispatch, getState) => {
  const { current, view } = getState();
  const list = getList(view);
  const path = list[(list.indexOf(current) + 1) % list.length];
  console.log(["next"], current, path);
  return dispatch(navigate(path));
};

const locationHash = (locationHash) => (dispatch) => {
  const [path, hash = "/włochy/"] =
    decodeURI(locationHash).match(/^#(.+)/) || [];
  console.log(["LOCATION_HASH"], path, hash);
  dispatch(navigatePath(hash));
};

const {
  increment,
  decrement,
  navigate,
  navigatePath,
  back,
  play,
  stop,
  setSpeed /*, actionThree*/,
} = createActions(
  {
    INCREMENT: (amount = 1) => amount,
    DECREMENT: (amount = 1) => amount,
    NAVIGATE: (path) => {
      console.log(["navigate"], path);
      // history.push(path, {});
      history.push(`#${path}`);
    },
    NAVIGATE_PATH: (path) => path,
    // BACK: () => null,
    BACK: () => {
      history.goBack();
    },
    PLAY: () => null,
    STOP: () => null,
    SET_SPEED: (timeout) => timeout,
  }
  // 'ACTION_THREE'
);

// const location = history.location;

const reducer = handleActions(
  new Map([
    [
      increment,
      (state, action) => ({
        ...state,
        counter: state.counter + action.payload,
      }),
    ],
    [
      decrement,
      (state, action) => ({
        ...state,
        counter: state.counter - action.payload,
      }),
    ],
    [
      // combineActions(navigate, next)
      navigatePath,
      (state, { payload: current }) => ({
        ...state,
        history: [...state.history, state.current],
        current,
      }),
    ],
    // [
    //   back,
    //   (state) => state.history.length > 0 ? {
    //     ...state,
    //     history: state.history.slice(0, state.history.length - 1),
    //     current: state.history[state.history.length - 1]
    //   } : state
    // ],
    [
      play,
      (state) => ({
        ...state,
        playing: true,
      }),
    ],
    [
      stop,
      (state) => ({
        ...state,
        playing: false,
      }),
    ],
    [
      setSpeed,
      (state, { payload: timeout }) => ({
        ...state,
        timeout,
      }),
    ],
  ]),
  {
    playing: false,
    timeout: 1000,
    counter: 0,
    // current: '/centrum-florencji/',
    current: "/",
    history: [],
    content: {
      [FLORENCJA]: {
        desc: "Florencja i Toskania",
        progress: 5,
      },
      [OLTRARNO]: {
        desc: "Oltrarno",
        progress: 3,
      },
    },
    view: {
      ...ITALY,
      ...METRO,
    },
  }
);

const store = createStore(reducer, applyMiddleware(thunk));

const history = createBrowserHistory({
  hashType: "slash",
});

console.log(["history.location"], history.location.hash);
store.dispatch(locationHash(history.location.hash));

// eslint-disable-next-line
const unlisten = history.listen((location, action) => {
  // location is an object like window.location
  console.log(["history.listen"], action, location.hash, location.state);
  store.dispatch(locationHash(location.hash));
});

const Link = ({ href, ...props }) => {
  const dispatch = useDispatch();
  return (
    <a
      href={href}
      onClick={(e) => e.preventDefault() || dispatch(navigate(href))}
      {...props}
    >
      {href}
    </a>
  );
};

const Item = ({ ...props }) => {
  return (
    <span>
      [<Link {...props} />]
    </span>
  );
};

const Navi = ({ path }) => {
  const { name, list } = useSelector((state) => state.view[path]);

  return (
    <span className={styles.Navi}>
      Navi [{name}]
      {list &&
        list.map((path, key) => (
          <Item href={path} key={key}>
            {path}
          </Item>
        ))}
    </span>
  );
};

// const getPath = (view, path = '/') => [path].concat(...(view[path].list || []).map(path => getList(view, path)));

const Path = ({ path }) => {
  // const { name, list } = useSelector(state => state.view[path]);
  const current = useSelector((state) => state.current);

  const list = current
    .split("/")
    .reduce(
      (curr, next) =>
        next ? curr.concat(curr[curr.length - 1] + next + "/") : curr,
      ["/"]
    );

  return (
    <span className={styles.Path}>
      Path
      {list &&
        list.map((path, key) => (
          <Item href={path} key={key}>
            {path}
          </Item>
        ))}
    </span>
  );
};

const Root = () => {
  const counter = useSelector((state) => state.counter);
  const current = useSelector((state) => state.current);
  const history = useSelector((state) => state.history);
  const playing = useSelector((state) => state.playing);
  const timeout = useSelector((state) => state.timeout);
  // const content = (content => content ? `${path}${content}/` : '')(current.substr(path.length).split('/')[0]);

  const dispatch = useDispatch();

  useEffect(() => {
    // console.log(['useEffect'], playing, current)
    const timer =
      playing &&
      setTimeout(() => {
        // console.log(['useEffect.timeout'], playing, current)
        // dispatch(increment())
        dispatch(next());
      }, timeout);
    if (timer) {
      return () => {
        clearTimeout(timer);
        // console.log(['useEffect.callback'], playing, current)
      };
    }
  }, [dispatch, playing, current, counter, timeout]);
  return (
    <div className={styles.Root}>
      <button onClick={(e) => dispatch(back())} disabled={history.length === 0}>
        &#171; Back
      </button>
      {playing ? (
        <button onClick={(e) => dispatch(stop())}>Stop</button>
      ) : (
        <button onClick={(e) => dispatch(play())}>Play</button>
      )}
      <select
        value={timeout}
        onChange={(e) =>
          console.log(e.target.value) ||
          dispatch(setSpeed(Number(e.target.value)))
        }
      >
        {[400, 1000, 2000].map((t) => (
          <option key={t} value={t}>{`${t}ms`}</option>
        ))}
        <option></option>
      </select>
      <button onClick={(e) => dispatch(next())}>Next &#8250;</button>
    </div>
  );
};

const View = ({ path = "/" }) => {
  const current = useSelector((state) => state.current);
  const content = ((content) => (content ? `${path}${content}/` : ""))(
    current.substr(path.length).split("/")[0]
  );

  // const dispatch = useDispatch();

  return (
    <div className={styles.View}>
      View
      <Item href={path}>{path}</Item>
      <Navi path={path} />
      <Content path={path} />
      {content && <View path={content} />}
    </div>
  );
};

const Content = ({ path }) => {
  const { name, related } = useSelector((state) => state.view[path]);
  const content = {
    path,
    name,
    ...useSelector((state) => state.content[related]),
  };
  return content ? (
    <div className={styles.Content}>
      Content [{path}][{related}]
      <pre className={styles.Pre}>{JSON.stringify(content, null, 2)}</pre>
    </div>
  ) : null;
};

const Progress = ({ onExited, ...props }) => {
  const { progress, marginTop, opacity } = useSpring({
    from: { opacity: 0 },
    to: {
      progress: props.progress,
      marginTop: props.progressTop,
      opacity: props.in ? 1 : 0,
    },
    config: config.slow,
    onRest: () => props.in === false && onExited(),
  });
  // console.log(['Progress'], props)
  return (
    <animated.div className={styles.Progress} style={{ opacity, marginTop }}>
      Progress
      <animated.div
        className={styles.Bar}
        style={{
          width: progress
            .interpolate([0, 5], [20, 100])
            .interpolate((w) => `${w}%`),
        }}
      >
        {progress.interpolate((x) => x.toFixed(0))}
      </animated.div>
    </animated.div>
  );
};

const Indicator = ({ onExited, ...props }) => {
  const { x, y, r, opacity } = useSpring({
    from: { opacity: 0, r: 0 },
    to: {
      x: props.x,
      y: props.y,
      r: props.r || 100,
      opacity: props.in ? 1 : 0,
    },
    config: config.wobbly,
    onRest: () => props.in === false && onExited(),
  });
  // console.log(['Indicator'], props)
  return (
    <animated.div
      className={styles.Indicator}
      style={{
        left: x.interpolate((w) => `${w}%`),
        top: y.interpolate((w) => `${w}%`),
        width: r,
        height: r,
        opacity,
      }}
    >
      Indicator
    </animated.div>
  );
};

const Flip = ({ onExited, flipped, ...props }) => {
  // const [flipped, set] = useState(false)
  const { transform, opacity } = useSpring({
    // from: {
    //   opacity:.5,
    //   transform: `perspective(600px) rotateX(${90}deg)`,
    // },
    to: {
      opacity: true ? (flipped ? 1 : 0) : 0,
      transform: `perspective(600px) rotateX(${
        true ? (flipped ? 180 : 0) : 90
      }deg)`,
    },
    config: { mass: 1, tension: 500, friction: 80 },
    // onRest: () => (props.in === false) && onExited()
  });
  return (
    <div className={styles.Flip}>
      <animated.div
        className={styles.Flip_Back}
        style={{
          opacity: opacity.interpolate((o) => (true ? 1 - o : 0)),
          transform,
        }}
      />
      <animated.div
        className={styles.Flip_Front}
        style={{
          opacity,
          transform: transform.interpolate((t) => `${t} rotateX(180deg)`),
        }}
      />
    </div>
  );
};

const Box = ({ onExited, in: inProp, exit, enter, appear, ...props }) => {
  // const {left, top, width, height, background} = useSpring({
  const { left, top, right, bottom, background } = useSpring({
    from: { left: 0, top: 0, right: 0, bottom: 0 },
    to: props,
    // to: { left }
  });
  console.log(["Box"], props);
  return (
    <animated.div
      className={styles.Box}
      style={{
        left: left.interpolate((v) => `${v}%`),
        top: top.interpolate((v) => `${v}%`),
        right: right.interpolate((v) => `${v}%`),
        bottom: bottom.interpolate((v) => `${v}%`),
        background,
      }}
    />
  );
};

const Wrapper = ({ in: show, onExited, children, ...props }) => {
  console.log(["Wrapper"], { show, onExited, children, ...props });
  const { opacity } = useSpring({
    from: { opacity: 1 },
    // immediate: true,
    // delay: show ? 100 : 0,
    to: show ? {} : { opacity: 0 },
    // config: { mass: 1, tension: 170, friction: show ? 30 : 20 },
    onRest: () => console.log(['onExited'], { show, onExited, children, ...props })||onExited(),
  });

  return (
    <animated.div
      style={{
        position: "absolute",
        opacity,
      }}
      className={styles.Wrapper}
    >
      {children}
    </animated.div>
  );
};

const Main = () => {
  const path = useSelector((state) => state.current);
  const {
    name,
    related,
    indicator,
    /*progress, */ template = TEMPLATE_2,
    ...view
  } = useSelector((state) => state.view[path]);
  const content = {
    indicator,
    progressTop: 0,
    flipped: null,
    // progress,
    path,
    name,
    ...useSelector((state) => state.content[related]),
    ...view,
  };
  const Template = {
    ...METRO_TEMPLATES,
    ...ITALY_TEMPLATES,
  }[template];
  console.log(["content"], content);
  return content ? (
    <div className={styles.Main}>
      Main [{path}][{template}]
      <TransitionGroup>
        {content.box && <Box {...content.box} />}
        {content.flipped !== null && <Flip flipped={content.flipped} />}
        {content.progress && (
          <Progress
            progress={content.progress}
            progressTop={content.progressTop}
          />
        )}
        {content.indicator && <Indicator {...content.indicator} />}
      </TransitionGroup>
      <TransitionGroup>
        <Wrapper key={path} path={path}>
          <Template {...content}>
            <pre className={styles.Pre}>{JSON.stringify(content, null, 2)}</pre>
          </Template>
        </Wrapper>
      </TransitionGroup>
    </div>
  ) : null;
};

const getList = (view, path = "/") =>
  [path].concat(...(view[path].list || []).map((path) => getList(view, path)));

const List = ({ path = "/" }) => {
  const current = useSelector((state) => state.current);
  const view = useSelector((state) => state.view);
  const list = getList(view);

  return (
    <div className={styles.List}>
      List
      {list.map((path, key) => (
        <Item
          key={key}
          href={path}
          className={cx(styles.Item, path === current && styles.Item__Current)}
        >
          {path}
        </Item>
      ))}
    </div>
  );
};

const Counter = () => {
  const state = useSelector((state) => state);
  const counter = useSelector((state) => state.counter);
  const dispatch = useDispatch();
  return (
    <div>
      <span>{counter}</span>
      <button onClick={() => dispatch(increment())}>+</button>
      <button onClick={() => dispatch(decrement())}>-</button>
      <pre className={styles.Pre}>{JSON.stringify(state, null, 2)}</pre>
    </div>
  );
};

