const Image = ({ in: show, onExited, src }) => {
  // console.log(["Wrapper"], { show, onExited, children, ...props });
  const { opacity } = useSpring({
    from: { opacity: 1 },
    // immediate: true,
    // delay: show ? 100 : 0,
    to: show ? {} : { opacity: 0 },
    // config: { mass: 1, tension: 170, friction: show ? 30 : 20 },
    onRest: () => console.log(["onExited"], { show, src }) || onExited(),
  });

  return (
    <animated.div
      className={styles.Image}
      style={{
        position: "absolute",
        opacity,
        background: `url(${src}) center / cover no-repeat`,
      }}
    ></animated.div>
  );
};

const FACTOR = 1;

const Gallery = ({ images, className }) => {
  const [active, setActive] = useState(0);

  const t = useRef();

  const wait = () =>
    console.log(["wait"], t.current) ||
    clearTimeout(t.current) ||
    setTimeout(next, 2000 * FACTOR);
  const plan = () =>
    console.log(["plan"], t.current) ||
    clearTimeout(t.current) ||
    setTimeout(next, 1500);
  const next = () => {
    // console.log(['next'])
    t.current = plan();
    // setActive(active => console.log(['next'], active)||(active === items.length - 1) ? 0 : (active + 1))
    setActive((active) =>
      console.log(["next"], active) || active === null
        ? 0
        : (active + 1) % images.length
    );
  };

  useEffect(() => {
    // t = (active !== null) && wait();
    t.current = wait();
    return () => {
      clearTimeout(t.current);
    };
  }, []);

  // const onEnter = (key) => setActive(key)||wait();
  const onEnter = (key) => {
    t.current = setActive(key) || wait();
  };

  return (
    <div className={cx(styles.Gallery, className)}>
      <TransitionGroup>
        <Image key={active} src={images[active]} />
      </TransitionGroup>

      {/* {images.map((src, key) => (
        <div
          key={key}
          className={styles.Img}
          style={{
            background: `url(${src}) center / cover no-repeat`,
          }}
        >
        </div>
      ))} */}
      {images.length === 1 ? null : (
        <div className={styles.Navi}>
          {images.map((src, key) => (
            <div
              key={key}
              className={cx(styles.Item, active === key && styles.active)}
              onMouseEnter={(e) => onEnter(key)}
            ></div>
          ))}
        </div>
      )}
    </div>
  );
};

const Preview = ({ in: show, onExited, images, name, info, path }) => {
  const { opacity, left } = useSpring({
    from: { opacity: 0, left: 0 },
    // immediate: true,
    delay: show ? 100 : 0,
    to: show ? { opacity: 1, left: 100 } : { opacity: 0, left: 0 },
    // to: show || fixed ? { opacity: 0.9 } : { opacity: 0.1 },
    config: { mass: 1, tension: 170, friction: show ? 30 : 20 },
    onRest: onExited,
  });

  return (
    <animated.div
      style={{
        opacity,
        // marginLeft: left.interpolate((left) => `${(5 * (100 - left)) / 100}%`),
        transform: left.interpolate(
          (left) => `translateX(${(5 * (100 - left)) / 100}%)`
        ),
      }}
      className={styles.Preview}
    >
      <a href={`#${path}`} className={styles.Text}>
        <div className={styles.Name}>{name}</div>
        {info && (
          <div className={styles.Info}>
            {info.map(({ icon, text }, key) => (
              <div key={key} className={styles.Item}>
                {icon && <Icon icon={ICONS[icon]} />} {text}
              </div>
            ))}
          </div>
        )}
        {images && <Gallery className={styles.Images} images={images} />}
        <div className={styles.Button}>Zobacz ofertę</div>
      </a>
    </animated.div>
  );
};

const Summary = ({ in: show, onExited, summary }) => {
  const { opacity, top } = useSpring({
    from: { opacity: 0, top: 0 },
    // immediate: true,
    delay: show ? 100 : 0,
    to: show ? { opacity: 1, top: 100 } : { opacity: 0, top: 0 },
    // to: show || fixed ? { opacity: 0.9 } : { opacity: 0.1 },
    config: { mass: 1, tension: 170, friction: show ? 30 : 20 },
    onRest: onExited,
  });

  return (
    <animated.div
      style={{
        opacity,
        // marginLeft: left.interpolate((left) => `${(5 * (100 - left)) / 100}%`),
        transform: top.interpolate(
          (top) => `translateY(${(5 * (100 - top)) / 100}%)`
        ),
      }}
      className={styles.Summary}
    >
      <Format>{summary}</Format>
    </animated.div>
  );
};

